export const wrapDataTable = (Table: FunctionComponent<any>): FunctionComponent<any> => {
  return ({ children, ...props }) => {
    const containerId = uuidv4();
    const dtSelector = `#${containerId} table`;
    /*
     * DataTable ã®è¨­å®š
     * - DataTable å…¨ä½“ã‚’ div ã§æ‹¬ã£ã¦ class "mb-3" ã‚’ä»˜ä¸Ž
     * - å…¨ã‚«ãƒ©ãƒ ã« "natural" ã‚½ãƒ¼ãƒˆã‚’æœ‰åŠ¹åŒ–
     * - å…¨ã‚«ãƒ©ãƒ ã®ã‚½ãƒ¼ãƒˆé †åºã‚’ "åˆæœŸé †åº"(ãƒ‡ãƒ•ã‚©ãƒ«ãƒˆ) => "æ˜‡é †" => "é™é †" ã«è¨­å®š
     * - ãƒšãƒ¼ã‚¸ãƒãƒ¼ã‚·ãƒ§ãƒ³ã‚’ç„¡åŠ¹åŒ–
     * - ãƒ†ãƒ¼ãƒ–ãƒ«ã‚’ç¸¦ã‚¹ã‚¯ãƒ­ãƒ¼ãƒ«åŒ–(ç¸¦å¹…ã¯ 500px)
     * - æ‹¡å¼µæ©Ÿèƒ½ã®ãƒœã‚¿ãƒ³ã‚’è¡¨ç¤º(ãƒœã‚¿ãƒ³ã¯ä»¥ä¸‹)
     *   - "Column visibility" ãƒœã‚¿ãƒ³: ã‚«ãƒ©ãƒ ã®è¡¨ç¤ºãƒ»éžè¡¨ç¤ºã‚’ãƒˆã‚°ãƒ«
     *   - "SearchPanels" ãƒœã‚¿ãƒ³: ã‚«ãƒ©ãƒ æ¯Žã®ãƒ•ã‚£ãƒ«ã‚¿ï¼ˆãƒ†ã‚­ã‚¹ãƒˆãƒ•ã‚£ãƒ«ã‚¿ã€é¸æŠžãƒ•ã‚£ãƒ«ã‚¿ï¼‰
     *   - "Copy": ãƒ†ãƒ¼ãƒ–ãƒ«ã®ã‚³ãƒ”ãƒ¼
     *   - "CSV": ãƒ†ãƒ¼ãƒ–ãƒ«ã‚’ CSV å½¢å¼ã§ãƒ€ã‚¦ãƒ³ãƒ­ãƒ¼ãƒ‰
     *   - "Print": ãƒ†ãƒ¼ãƒ–ãƒ«ã‚’å°åˆ·
     */
    const dataTableOptions = {
      dom: '<"mb-3"<"container-fluid"<"d-flex justify-content-between"fB>>>t<"text-muted"i>lp>',
      columnDefs: [{ type: 'natural', orderSequence: ['asc', 'desc', 'pre'], searchPanes: { show: true }, targets: '_all' }],
      order: [[0, 'pre']],
      paging: false,
      scrollCollapse: true,
      scrollY: '500px',
      select: true,
      buttons: ['colvis', 'searchPanes', 'spacer', 'copyHtml5', 'spacer', 'csvHtml5', 'spacer', 'print'],
    };

    // [MEMO] useEffect ã‚’ä½¿ã†ã¨ ReactCurrentDispatcher ãŒ null ã«ãªã‚‹
    // (ãŠãã‚‰ã plugin ãŒèª­ã¿è¾¼ã‚€ react ã‚¤ãƒ³ã‚¹ã‚¿ãƒ³ã‚¹ãŒ app(GROWI) ã¨ç•°ãªã‚‹ãŸã‚)
    // ãã“ã§ã€async-react ã‚’ä½¿ã£ã¦ã€plugin ã‚’æœ‰åŠ¹åŒ–ã™ã‚‹ãŸã‚ã®ã‚¤ãƒ™ãƒ³ãƒˆå‡¦ç†ã‚’è¡Œã£ã¦ã„ã‚‹
    const enableDataTable = async () => {
      if (DataTable.isDataTable(dtSelector)) return;

      const api = new DataTable(dtSelector, dataTableOptions as ConfigWeaken);

      api.on('order.dt', () => {
        const order = api.order();
        if (order.length <= 0) return;

        const orderSequenceWillBe = order[0][1];
        if ((orderSequenceWillBe as OrderExtend) !== 'pre') return;

        (api.order as any).neutral().draw();
      });

      // ã©ã“ã‹ã§ã‚½ãƒ¼ãƒˆé †åºãŒå¤‰ã‚ã‚‹ã®ã§æ˜Žç¤ºçš„ã«å…ƒã®é †åºã‚’è¨­å®šã™ã‚‹(issue#9)
      (api.order as any).neutral().draw();
    };

    return (
      <Async promiseFn={enableDataTable}>
        <div id={containerId} className="position-relative">
          <Table {...props}>{children}</Table>
        </div>
      </Async>
    );
  };
};
